"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { PhoneState } from "@/components/GameInterface";
import { SettingsApp } from "@/components/apps/SettingsApp";
import { FileManagerApp } from "@/components/apps/FileManagerApp";
import { TerminalApp } from "@/components/apps/TerminalApp";
import { AntivirusApp } from "@/components/apps/AntivirusApp";
import { StatusApp } from "@/components/apps/StatusApp";
import { MessagesApp } from "@/components/apps/MessagesApp";
import { BatteryApp } from "@/components/apps/BatteryApp";
import { SecurityApp } from "@/components/apps/SecurityApp";

interface PhoneScreenProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  isPlayer: boolean;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  showTutorial: boolean;
  onSkipTutorial: () => void;
}

interface App {
  id: string;
  name: string;
  icon: string;
  color: string;
  position: { x: number; y: number };
  component?: React.ComponentType<any>;
}

export function PhoneScreen({ 
  phoneState, 
  setPhoneState, 
  isPlayer, 
  onAttack, 
  onDefend,
  showTutorial,
  onSkipTutorial 
}: PhoneScreenProps) {
  const [openApp, setOpenApp] = useState<string | null>(null);
  const [apps, setApps] = useState<App[]>([]);

  // Initialize apps with random positions
  useEffect(() => {
    const baseApps: Omit<App, 'position'>[] = [
      { id: 'settings', name: 'Settings', icon: '⚙️', color: 'bg-gray-600', component: SettingsApp },
      { id: 'files', name: 'Files', icon: '📁', color: 'bg-blue-600', component: FileManagerApp },
      { id: 'terminal', name: 'Terminal', icon: '💻', color: 'bg-black', component: TerminalApp },
      { id: 'antivirus', name: 'Antivirus', icon: '🛡️', color: 'bg-green-600', component: AntivirusApp },
      { id: 'status', name: 'Status', icon: '📊', color: 'bg-purple-600', component: StatusApp },
      { id: 'messages', name: 'Messages', icon: '💬', color: 'bg-blue-500', component: MessagesApp },
      { id: 'battery', name: 'Battery', icon: '🔋', color: 'bg-yellow-600', component: BatteryApp },
      { id: 'security', name: 'Security', icon: '🔒', color: 'bg-red-600', component: SecurityApp },
    ];

    // Generate random grid positions (4x6 grid)
    const positions = [];
    for (let row = 0; row < 6; row++) {
      for (let col = 0; col < 4; col++) {
        positions.push({ x: col, y: row });
      }
    }
    
    // Shuffle positions
    const shuffledPositions = positions.sort(() => Math.random() - 0.5);
    
    const appsWithPositions = baseApps.map((app, index) => ({
      ...app,
      position: shuffledPositions[index]
    }));

    setApps(appsWithPositions);
  }, []);

  const handleAppClick = (appId: string) => {
    if (!isPlayer) return; // Only player can interact with their phone
    setOpenApp(appId);
  };

  const closeApp = () => {
    setOpenApp(null);
  };

  const currentApp = apps.find(app => app.id === openApp);

  return (
    <div className="relative">
      {/* Phone Frame */}
      <div className="bg-black rounded-[3rem] p-4 shadow-2xl border-4 border-gray-800 max-w-sm mx-auto">
        {/* Screen */}
        <div className="bg-gradient-to-br from-blue-900 via-purple-900 to-pink-900 rounded-[2.5rem] h-[600px] relative overflow-hidden">
          
          {/* Status Bar */}
          <div className="flex justify-between items-center p-4 text-white text-sm">
            <div className="flex items-center space-x-2">
              <div className="w-4 h-2 bg-white rounded-sm"></div>
              <span className="text-xs">Sabotage OS</span>
            </div>
            <div className="flex items-center space-x-1">
              <span className="text-xs">{phoneState.battery}%</span>
              <div className={`w-6 h-3 border border-white rounded-sm ${
                phoneState.battery > 20 ? 'bg-green-500' : 'bg-red-500'
              }`}></div>
            </div>
          </div>

          {/* Health Bar */}
          <div className="px-4 mb-4">
            <div className="bg-black/30 rounded-full h-2">
              <div 
                className={`h-full rounded-full transition-all duration-300 ${
                  phoneState.health > 50 ? 'bg-green-500' : phoneState.health > 25 ? 'bg-yellow-500' : 'bg-red-500'
                }`}
                style={{ width: `${phoneState.health}%` }}
              ></div>
            </div>
            <p className="text-white text-xs text-center mt-1">System Health: {phoneState.health}%</p>
          </div>

          {/* App or Home Screen */}
          {openApp && currentApp?.component ? (
            <div className="flex-1 p-4">
              <div className="bg-black/80 backdrop-blur-lg rounded-2xl h-full p-4">
                <div className="flex justify-between items-center mb-4">
                  <h3 className="text-white font-semibold">{currentApp.name}</h3>
                  <Button 
                    onClick={closeApp}
                    variant="ghost"
                    size="sm"
                    className="text-white hover:bg-white/20"
                  >
                    ✕
                  </Button>
                </div>
                <currentApp.component 
                  phoneState={phoneState}
                  setPhoneState={setPhoneState}
                  onAttack={onAttack}
                  onDefend={onDefend}
                  isPlayer={isPlayer}
                />
              </div>
            </div>
          ) : (
            /* Home Screen with Apps */
            <div className="flex-1 p-4">
              <div className="grid grid-cols-4 gap-4 h-full">
                {apps.map((app) => (
                  <div
                    key={app.id}
                    className="flex flex-col items-center cursor-pointer"
                    style={{
                      gridColumn: app.position.x + 1,
                      gridRow: app.position.y + 1,
                    }}
                    onClick={() => handleAppClick(app.id)}
                  >
                    <div className={`w-12 h-12 ${app.color} rounded-2xl flex items-center justify-center text-white text-lg shadow-lg hover:scale-105 transition-transform ${!isPlayer ? 'opacity-50' : ''}`}>
                      {app.icon}
                    </div>
                    <span className="text-white text-xs mt-1 text-center">{app.name}</span>
                  </div>
                ))}
              </div>
            </div>
          )}

          {/* Virus/Attack Effects */}
          {phoneState.virusCount > 0 && (
            <div className="absolute inset-0 pointer-events-none">
              <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 text-red-500 text-6xl animate-pulse">
                ⚠️
              </div>
            </div>
          )}

          {/* Low Health Effect */}
          {phoneState.health < 25 && (
            <div className="absolute inset-0 bg-red-500/20 pointer-events-none animate-pulse"></div>
          )}
        </div>
      </div>

      {/* Player Label */}
      <div className="text-center mt-2">
        <span className={`text-sm font-semibold ${isPlayer ? 'text-green-400' : 'text-red-400'}`}>
          {isPlayer ? 'YOUR PHONE' : 'ENEMY PHONE'}
        </span>
      </div>
    </div>
  );
}
