"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";

interface ShopProps {
  onBack: () => void;
  gigabytes: number;
  setGigabytes: React.Dispatch<React.SetStateAction<number>>;
}

interface ShopItem {
  id: string;
  name: string;
  description: string;
  price: number;
  icon: string;
  category: 'defense' | 'attack' | 'utility';
  owned: boolean;
}

export function Shop({ onBack, gigabytes, setGigabytes }: ShopProps) {
  const [selectedCategory, setSelectedCategory] = useState<'all' | 'defense' | 'attack' | 'utility'>('all');
  const [ownedItems, setOwnedItems] = useState<Set<string>>(new Set());

  const shopItems: ShopItem[] = [
    // Defense Items
    {
      id: 'premium_antivirus',
      name: 'Premium Antivirus',
      description: 'Advanced virus protection with real-time scanning',
      price: 150,
      icon: '🛡️',
      category: 'defense',
      owned: ownedItems.has('premium_antivirus')
    },
    {
      id: 'military_firewall',
      name: 'Military Firewall',
      description: 'Military-grade firewall protection',
      price: 200,
      icon: '🔥',
      category: 'defense',
      owned: ownedItems.has('military_firewall')
    },
    {
      id: 'auto_repair',
      name: 'Auto Repair System',
      description: 'Automatically repairs system damage',
      price: 180,
      icon: '🔧',
      category: 'defense',
      owned: ownedItems.has('auto_repair')
    },
    {
      id: 'backup_shield',
      name: 'Backup Shield',
      description: 'Creates automatic backups during attacks',
      price: 120,
      icon: '💾',
      category: 'defense',
      owned: ownedItems.has('backup_shield')
    },
    
    // Attack Items
    {
      id: 'mega_virus',
      name: 'Mega Virus',
      description: 'Devastating virus that deals massive damage',
      price: 250,
      icon: '💣',
      category: 'attack',
      owned: ownedItems.has('mega_virus')
    },
    {
      id: 'quantum_ddos',
      name: 'Quantum DDoS',
      description: 'Next-gen DDoS attack with quantum processing',
      price: 300,
      icon: '⚡',
      category: 'attack',
      owned: ownedItems.has('quantum_ddos')
    },
    {
      id: 'data_shredder',
      name: 'Data Shredder',
      description: 'Permanently destroys enemy files',
      price: 220,
      icon: '🗑️',
      category: 'attack',
      owned: ownedItems.has('data_shredder')
    },
    {
      id: 'logic_bomb',
      name: 'Logic Bomb',
      description: 'Delayed attack that triggers automatically',
      price: 280,
      icon: '💥',
      category: 'attack',
      owned: ownedItems.has('logic_bomb')
    },
    
    // Utility Items
    {
      id: 'battery_booster',
      name: 'Battery Booster',
      description: 'Increases maximum battery capacity',
      price: 100,
      icon: '🔋',
      category: 'utility',
      owned: ownedItems.has('battery_booster')
    },
    {
      id: 'cpu_optimizer',
      name: 'CPU Optimizer',
      description: 'Reduces CPU usage and improves performance',
      price: 130,
      icon: '⚙️',
      category: 'utility',
      owned: ownedItems.has('cpu_optimizer')
    },
    {
      id: 'ram_expander',
      name: 'RAM Expander',
      description: 'Increases available memory',
      price: 110,
      icon: '💿',
      category: 'utility',
      owned: ownedItems.has('ram_expander')
    },
    {
      id: 'speed_hack',
      name: 'Speed Hack',
      description: 'Reduces attack cooldowns',
      price: 160,
      icon: '🚀',
      category: 'utility',
      owned: ownedItems.has('speed_hack')
    }
  ];

  const filteredItems = shopItems.filter(item => 
    selectedCategory === 'all' || item.category === selectedCategory
  );

  const purchaseItem = (item: ShopItem) => {
    if (gigabytes >= item.price && !ownedItems.has(item.id)) {
      setGigabytes(prev => prev - item.price);
      setOwnedItems(prev => new Set([...prev, item.id]));
    }
  };

  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'defense': return 'text-green-400';
      case 'attack': return 'text-red-400';
      case 'utility': return 'text-blue-400';
      default: return 'text-gray-400';
    }
  };

  return (
    <div className="min-h-screen p-4">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-6 mb-6 border border-gray-700">
          <div className="flex justify-between items-center mb-4">
            <h1 className="text-3xl font-bold text-white">SHOP</h1>
            <Button 
              onClick={onBack}
              variant="outline"
              className="bg-gray-800 border-gray-600 text-white hover:bg-gray-700"
            >
              ← Back
            </Button>
          </div>
          
          <div className="bg-gray-800 rounded-2xl p-4 text-center">
            <p className="text-green-400 font-mono text-2xl">{gigabytes} GB</p>
            <p className="text-gray-400">Available Currency</p>
          </div>
        </div>

        {/* Category Filter */}
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-6 mb-6 border border-gray-700">
          <h2 className="text-white font-semibold mb-4">Categories</h2>
          <div className="flex space-x-2">
            {['all', 'defense', 'attack', 'utility'].map((category) => (
              <Button
                key={category}
                onClick={() => setSelectedCategory(category as any)}
                className={`${
                  selectedCategory === category 
                    ? 'bg-blue-600 hover:bg-blue-700' 
                    : 'bg-gray-700 hover:bg-gray-600'
                } text-white capitalize`}
              >
                {category}
              </Button>
            ))}
          </div>
        </div>

        {/* Shop Items */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {filteredItems.map((item) => (
            <div key={item.id} className="bg-black/80 backdrop-blur-lg rounded-2xl p-6 border border-gray-700">
              <div className="text-center mb-4">
                <div className="text-4xl mb-2">{item.icon}</div>
                <h3 className="text-white font-semibold text-lg">{item.name}</h3>
                <p className={`text-sm capitalize ${getCategoryColor(item.category)}`}>
                  {item.category}
                </p>
              </div>
              
              <p className="text-gray-300 text-sm mb-4 text-center leading-relaxed">
                {item.description}
              </p>
              
              <div className="text-center mb-4">
                <span className="text-green-400 font-mono text-xl">{item.price} GB</span>
              </div>
              
              <Button
                onClick={() => purchaseItem(item)}
                disabled={gigabytes < item.price || ownedItems.has(item.id)}
                className={`w-full ${
                  ownedItems.has(item.id)
                    ? 'bg-green-600 hover:bg-green-600 cursor-not-allowed'
                    : gigabytes >= item.price
                      ? 'bg-blue-600 hover:bg-blue-700'
                      : 'bg-gray-600 hover:bg-gray-600 cursor-not-allowed'
                } text-white font-semibold py-3`}
              >
                {ownedItems.has(item.id) ? 'OWNED' : 'PURCHASE'}
              </Button>
            </div>
          ))}
        </div>

        {/* Owned Items Summary */}
        {ownedItems.size > 0 && (
          <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-6 mt-6 border border-gray-700">
            <h2 className="text-white font-semibold mb-4">Your Arsenal</h2>
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              {Array.from(ownedItems).map((itemId) => {
                const item = shopItems.find(i => i.id === itemId);
                if (!item) return null;
                
                return (
                  <div key={itemId} className="bg-gray-800 rounded-lg p-3 text-center">
                    <div className="text-2xl mb-1">{item.icon}</div>
                    <div className="text-white text-sm font-medium">{item.name}</div>
                    <div className={`text-xs ${getCategoryColor(item.category)}`}>
                      {item.category}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        )}

        {/* Tips */}
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-6 mt-6 border border-gray-700">
          <h2 className="text-white font-semibold mb-4">💡 Shopping Tips</h2>
          <div className="space-y-2 text-gray-300 text-sm">
            <p>• Defense items help protect your phone from enemy attacks</p>
            <p>• Attack items give you more powerful offensive capabilities</p>
            <p>• Utility items improve your phone's overall performance</p>
            <p>• Earn more Gigabytes by winning battles and dealing damage</p>
            <p>• Purchased items are permanent upgrades to your arsenal</p>
          </div>
        </div>
      </div>
    </div>
  );
}
