"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { PhoneState } from "@/components/GameInterface";

interface FileItem {
  id: string;
  name: string;
  type: 'folder' | 'file';
  size?: string;
  icon: string;
  critical?: boolean;
  infected?: boolean;
}

interface FileManagerAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function FileManagerApp({ phoneState, setPhoneState, onAttack, onDefend, isPlayer }: FileManagerAppProps) {
  const [currentPath, setCurrentPath] = useState('/');
  const [files, setFiles] = useState<FileItem[]>([]);
  const [selectedFile, setSelectedFile] = useState<string | null>(null);

  useEffect(() => {
    // Generate file system based on phone state
    const systemFiles: FileItem[] = [
      { id: '1', name: 'System', type: 'folder', icon: '🗂️', critical: true },
      { id: '2', name: 'Apps', type: 'folder', icon: '📱' },
      { id: '3', name: 'Documents', type: 'folder', icon: '📄' },
      { id: '4', name: 'Downloads', type: 'folder', icon: '⬇️' },
      { id: '5', name: 'boot.sys', type: 'file', size: '2.1 MB', icon: '⚙️', critical: true },
      { id: '6', name: 'kernel.dll', type: 'file', size: '5.8 MB', icon: '🔧', critical: true },
      { id: '7', name: 'config.ini', type: 'file', size: '1.2 KB', icon: '📝' },
      { id: '8', name: 'user_data.db', type: 'file', size: '15.3 MB', icon: '💾' },
    ];

    // Add infected files based on virus count
    const infectedFiles = [];
    for (let i = 0; i < phoneState.virusCount; i++) {
      infectedFiles.push({
        id: `virus_${i}`,
        name: `malware_${i + 1}.exe`,
        type: 'file' as const,
        size: '666 KB',
        icon: '🦠',
        infected: true
      });
    }

    // Remove files based on filesDeleted count
    const availableFiles = [...systemFiles, ...infectedFiles];
    const filesToShow = availableFiles.slice(0, Math.max(4, availableFiles.length - phoneState.filesDeleted));

    setFiles(filesToShow);
  }, [phoneState.virusCount, phoneState.filesDeleted]);

  const handleFileClick = (file: FileItem) => {
    setSelectedFile(selectedFile === file.id ? null : file.id);
  };

  const deleteFile = (fileId: string) => {
    if (!isPlayer) return;
    
    const file = files.find(f => f.id === fileId);
    if (!file) return;

    if (file.critical) {
      // Deleting critical files damages the system
      setPhoneState(prev => ({
        ...prev,
        health: Math.max(0, prev.health - 20),
        filesDeleted: prev.filesDeleted + 1
      }));
    } else if (file.infected) {
      // Deleting infected files helps clean the system
      setPhoneState(prev => ({
        ...prev,
        virusCount: Math.max(0, prev.virusCount - 1),
        health: Math.min(100, prev.health + 5)
      }));
    }

    setFiles(prev => prev.filter(f => f.id !== fileId));
    setSelectedFile(null);
  };

  const restoreFile = () => {
    if (!isPlayer) return;
    
    // Restore a random deleted file
    const restoredFile: FileItem = {
      id: `restored_${Date.now()}`,
      name: 'recovered_file.bak',
      type: 'file',
      size: '2.5 MB',
      icon: '🔄'
    };

    setFiles(prev => [...prev, restoredFile]);
    setPhoneState(prev => ({
      ...prev,
      health: Math.min(100, prev.health + 10),
      filesDeleted: Math.max(0, prev.filesDeleted - 1)
    }));
  };

  const scanForViruses = () => {
    if (!isPlayer) return;
    
    // Highlight infected files
    const infectedCount = files.filter(f => f.infected).length;
    if (infectedCount > 0) {
      // Auto-select first infected file
      const firstInfected = files.find(f => f.infected);
      if (firstInfected) {
        setSelectedFile(firstInfected.id);
      }
    }
  };

  const createBackup = () => {
    if (!isPlayer) return;
    
    const backupFile: FileItem = {
      id: `backup_${Date.now()}`,
      name: `backup_${new Date().toISOString().slice(0, 10)}.bak`,
      type: 'file',
      size: '50.2 MB',
      icon: '💾'
    };

    setFiles(prev => [...prev, backupFile]);
    onDefend('backup');
  };

  const selectedFileData = files.find(f => f.id === selectedFile);

  return (
    <div className="h-full flex flex-col space-y-4">
      {/* Path and Actions */}
      <div className="bg-gray-800 rounded-lg p-3">
        <div className="flex justify-between items-center mb-2">
          <div className="text-white font-mono text-sm">{currentPath}</div>
          <div className="text-gray-400 text-xs">{files.length} items</div>
        </div>
        
        <div className="flex space-x-2">
          <Button 
            onClick={scanForViruses}
            disabled={!isPlayer}
            size="sm"
            className="bg-red-600 hover:bg-red-700 text-xs"
          >
            Scan Viruses
          </Button>
          <Button 
            onClick={restoreFile}
            disabled={!isPlayer || phoneState.filesDeleted === 0}
            size="sm"
            className="bg-blue-600 hover:bg-blue-700 text-xs"
          >
            Restore File
          </Button>
          <Button 
            onClick={createBackup}
            disabled={!isPlayer}
            size="sm"
            className="bg-green-600 hover:bg-green-700 text-xs"
          >
            Backup
          </Button>
        </div>
      </div>

      {/* File List */}
      <div className="flex-1 bg-gray-800 rounded-lg p-3 overflow-y-auto">
        <div className="space-y-1">
          {files.map((file) => (
            <div
              key={file.id}
              onClick={() => handleFileClick(file)}
              className={`flex items-center justify-between p-2 rounded cursor-pointer transition-colors ${
                selectedFile === file.id 
                  ? 'bg-blue-600' 
                  : file.infected 
                    ? 'bg-red-900/30 hover:bg-red-800/30' 
                    : file.critical 
                      ? 'bg-yellow-900/30 hover:bg-yellow-800/30'
                      : 'hover:bg-gray-700'
              } ${!isPlayer ? 'opacity-50 cursor-not-allowed' : ''}`}
            >
              <div className="flex items-center space-x-3">
                <span className="text-lg">{file.icon}</span>
                <div>
                  <div className={`text-sm ${
                    file.infected ? 'text-red-400' : 
                    file.critical ? 'text-yellow-400' : 'text-white'
                  }`}>
                    {file.name}
                  </div>
                  {file.size && (
                    <div className="text-xs text-gray-400">{file.size}</div>
                  )}
                </div>
              </div>
              
              <div className="flex items-center space-x-1">
                {file.critical && <span className="text-yellow-400 text-xs">⚠️</span>}
                {file.infected && <span className="text-red-400 text-xs">🦠</span>}
                <span className="text-gray-400 text-xs">{file.type}</span>
              </div>
            </div>
          ))}
          
          {files.length === 0 && (
            <div className="text-center text-gray-400 py-8">
              <div className="text-4xl mb-2">📁</div>
              <div>No files found</div>
              <div className="text-xs mt-1">System files may have been deleted</div>
            </div>
          )}
        </div>
      </div>

      {/* File Actions */}
      {selectedFileData && (
        <div className="bg-gray-800 rounded-lg p-3">
          <h4 className="text-white font-semibold mb-2">File Actions</h4>
          <div className="flex space-x-2">
            <Button 
              onClick={() => deleteFile(selectedFileData.id)}
              disabled={!isPlayer}
              size="sm"
              variant="destructive"
              className="bg-red-600 hover:bg-red-700"
            >
              Delete
            </Button>
            
            {selectedFileData.infected && (
              <Button 
                onClick={() => deleteFile(selectedFileData.id)}
                disabled={!isPlayer}
                size="sm"
                className="bg-green-600 hover:bg-green-700"
              >
                Clean Virus
              </Button>
            )}
          </div>
          
          <div className="mt-2 text-xs text-gray-400">
            {selectedFileData.critical && "⚠️ Critical system file - deletion will damage system"}
            {selectedFileData.infected && "🦠 Infected file - safe to delete"}
          </div>
        </div>
      )}

      {/* Storage Info */}
      <div className="bg-gray-800 rounded-lg p-3">
        <h4 className="text-white font-semibold mb-2">Storage</h4>
        <div className="space-y-1 text-xs">
          <div className="flex justify-between">
            <span className="text-gray-300">Used Space</span>
            <span className="text-white">{85 + phoneState.virusCount * 5}%</span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-300">System Files</span>
            <span className={phoneState.filesDeleted > 0 ? 'text-red-400' : 'text-green-400'}>
              {Math.max(0, 8 - phoneState.filesDeleted)}/8
            </span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-300">Infected Files</span>
            <span className={phoneState.virusCount > 0 ? 'text-red-400' : 'text-green-400'}>
              {phoneState.virusCount}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
}
