"use client";

import { useState, useEffect } from "react";
import { HomeMenu } from "@/components/HomeMenu";
import { GameInterface } from "@/components/GameInterface";
import { Shop } from "@/components/Shop";
import { Settings } from "@/components/Settings";

export default function Page() {
  const [currentScreen, setCurrentScreen] = useState<'home' | 'game' | 'shop' | 'settings'>('home');
  const [gigabytes, setGigabytes] = useState(100);
  const [showTutorial, setShowTutorial] = useState(true);

  const handleWin = (earnedGB: number) => {
    setGigabytes(prev => prev + earnedGB);
    setCurrentScreen('home');
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-purple-900 to-gray-900">
      {currentScreen === 'home' && (
        <HomeMenu 
          onStartGame={() => setCurrentScreen('game')}
          onOpenShop={() => setCurrentScreen('shop')}
          onOpenSettings={() => setCurrentScreen('settings')}
          gigabytes={gigabytes}
          showTutorial={showTutorial}
          onSkipTutorial={() => setShowTutorial(false)}
        />
      )}
      {currentScreen === 'game' && (
        <GameInterface 
          onBack={() => setCurrentScreen('home')}
          onWin={handleWin}
          showTutorial={showTutorial}
          onSkipTutorial={() => setShowTutorial(false)}
        />
      )}
      {currentScreen === 'shop' && (
        <Shop 
          onBack={() => setCurrentScreen('home')}
          gigabytes={gigabytes}
          setGigabytes={setGigabytes}
        />
      )}
      {currentScreen === 'settings' && (
        <Settings onBack={() => setCurrentScreen('home')} />
      )}
    </div>
  );
}
