"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";

interface HomeMenuProps {
  onStartGame: () => void;
  onOpenShop: () => void;
  onOpenSettings: () => void;
  gigabytes: number;
  showTutorial: boolean;
  onSkipTutorial: () => void;
}

export function HomeMenu({ onStartGame, onOpenShop, onOpenSettings, gigabytes, showTutorial, onSkipTutorial }: HomeMenuProps) {
  const [tutorialStep, setTutorialStep] = useState(0);

  const tutorialSteps = [
    "Welcome to Sabotage! Your goal is to destroy your opponent's phone while protecting yours.",
    "Use apps on your phone to attack and defend. Each app has a specific purpose.",
    "The Terminal lets you run system commands, Antivirus protects you from attacks.",
    "Monitor your phone's health in the Status app. When it reaches 0%, you lose!",
    "Win matches to earn Gigabytes and upgrade your defenses in the Shop."
  ];

  if (showTutorial) {
    return (
      <div className="min-h-screen flex items-center justify-center p-4">
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-8 max-w-md w-full border border-gray-700">
          <h2 className="text-2xl font-bold text-white mb-6 text-center">Tutorial</h2>
          <div className="bg-gray-800 rounded-2xl p-6 mb-6">
            <p className="text-gray-200 text-center leading-relaxed">
              {tutorialSteps[tutorialStep]}
            </p>
          </div>
          <div className="flex justify-between">
            <Button 
              onClick={onSkipTutorial}
              variant="outline"
              className="bg-gray-700 border-gray-600 text-white hover:bg-gray-600"
            >
              Skip Tutorial
            </Button>
            {tutorialStep < tutorialSteps.length - 1 ? (
              <Button 
                onClick={() => setTutorialStep(prev => prev + 1)}
                className="bg-blue-600 hover:bg-blue-700"
              >
                Next
              </Button>
            ) : (
              <Button 
                onClick={onSkipTutorial}
                className="bg-green-600 hover:bg-green-700"
              >
                Start Playing!
              </Button>
            )}
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen flex items-center justify-center p-4">
      <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-8 max-w-md w-full border border-gray-700">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-white mb-2">SABOTAGE</h1>
          <p className="text-gray-400">Mobile OS Battle Arena</p>
        </div>
        
        <div className="bg-gray-800 rounded-2xl p-4 mb-6 text-center">
          <p className="text-green-400 font-mono text-lg">{gigabytes} GB</p>
          <p className="text-gray-400 text-sm">Available Currency</p>
        </div>

        <div className="space-y-4">
          <Button 
            onClick={onStartGame}
            className="w-full bg-red-600 hover:bg-red-700 text-white py-4 text-lg font-semibold rounded-2xl"
          >
            START BATTLE
          </Button>
          
          <Button 
            onClick={onOpenShop}
            className="w-full bg-blue-600 hover:bg-blue-700 text-white py-4 text-lg font-semibold rounded-2xl"
          >
            SHOP
          </Button>
          
          <Button 
            onClick={onOpenSettings}
            className="w-full bg-gray-600 hover:bg-gray-700 text-white py-4 text-lg font-semibold rounded-2xl"
          >
            SETTINGS
          </Button>
        </div>

        <div className="mt-8 text-center">
          <p className="text-gray-500 text-sm">v1.0.0 - Destroy or be destroyed</p>
        </div>
      </div>
    </div>
  );
}
