"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { PhoneState } from "@/components/GameInterface";

interface TerminalAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function TerminalApp({ phoneState, setPhoneState, onAttack, onDefend, isPlayer }: TerminalAppProps) {
  const [command, setCommand] = useState("");
  const [output, setOutput] = useState<string[]>([
    "Sabotage OS Terminal v1.0",
    "Type 'help' for available commands",
    ""
  ]);

  const executeCommand = () => {
    if (!command.trim()) return;

    const newOutput = [...output, `$ ${command}`];
    
    switch (command.toLowerCase().trim()) {
      case 'help':
        newOutput.push(
          "Available commands:",
          "virus <target> - Deploy virus attack (15 damage)",
          "ddos <target> - DDoS attack (20 damage)", 
          "battery_drain <target> - Drain battery (10 damage)",
          "file_delete <target> - Delete system files (25 damage)",
          "cpu_overload <target> - Overload CPU (12 damage)",
          "ram_flood <target> - Flood RAM (18 damage)",
          "repair - Repair system damage",
          "firewall - Enable firewall protection",
          "clear - Clear terminal",
          ""
        );
        break;
      
      case 'virus enemy':
      case 'virus target':
        if (isPlayer) {
          onAttack('virus', 15);
          newOutput.push("Virus deployed successfully!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'ddos enemy':
      case 'ddos target':
        if (isPlayer) {
          onAttack('ddos', 20);
          newOutput.push("DDoS attack initiated!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'battery_drain enemy':
      case 'battery_drain target':
        if (isPlayer) {
          onAttack('battery_drain', 10);
          newOutput.push("Battery drain attack executed!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'file_delete enemy':
      case 'file_delete target':
        if (isPlayer) {
          onAttack('file_delete', 25);
          newOutput.push("Critical files deleted from target!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'cpu_overload enemy':
      case 'cpu_overload target':
        if (isPlayer) {
          onAttack('cpu_overload', 12);
          newOutput.push("CPU overload attack successful!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'ram_flood enemy':
      case 'ram_flood target':
        if (isPlayer) {
          onAttack('ram_flood', 18);
          newOutput.push("RAM flooding initiated!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'repair':
        if (isPlayer) {
          onDefend('repair');
          newOutput.push("System repair initiated...", "Health restored!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'firewall':
        if (isPlayer) {
          onDefend('firewall');
          newOutput.push("Firewall activated!", "");
        } else {
          newOutput.push("Access denied.", "");
        }
        break;
        
      case 'clear':
        setOutput(["Sabotage OS Terminal v1.0", "Type 'help' for available commands", ""]);
        setCommand("");
        return;
        
      case 'status':
        newOutput.push(
          `System Status:`,
          `Health: ${phoneState.health}%`,
          `Battery: ${phoneState.battery}%`,
          `CPU Usage: ${phoneState.cpuUsage}%`,
          `RAM Usage: ${phoneState.ramUsage}%`,
          `Viruses: ${phoneState.virusCount}`,
          `Antivirus: ${phoneState.antivirusActive ? 'Active' : 'Inactive'}`,
          `Firewall: ${phoneState.firewallActive ? 'Active' : 'Inactive'}`,
          ""
        );
        break;
        
      default:
        newOutput.push(`Command not found: ${command}`, "Type 'help' for available commands", "");
    }
    
    setOutput(newOutput);
    setCommand("");
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter') {
      executeCommand();
    }
  };

  return (
    <div className="h-full flex flex-col">
      <div className="flex-1 bg-black rounded-lg p-3 font-mono text-sm overflow-y-auto">
        {output.map((line, index) => (
          <div key={index} className="text-green-400 whitespace-pre-wrap">
            {line}
          </div>
        ))}
      </div>
      
      <div className="mt-3 flex space-x-2">
        <Input
          value={command}
          onChange={(e) => setCommand(e.target.value)}
          onKeyPress={handleKeyPress}
          placeholder="Enter command..."
          className="bg-gray-800 border-gray-600 text-white font-mono"
          disabled={!isPlayer}
        />
        <Button 
          onClick={executeCommand}
          className="bg-green-600 hover:bg-green-700"
          disabled={!isPlayer}
        >
          Run
        </Button>
      </div>
    </div>
  );
}
