"use client";

import { useState, useEffect, useCallback } from "react";
import { Button } from "@/components/ui/button";
import { PhoneScreen } from "@/components/PhoneScreen";
import { BotAI } from "@/components/BotAI";

interface GameInterfaceProps {
  onBack: () => void;
  onWin: (earnedGB: number) => void;
  showTutorial: boolean;
  onSkipTutorial: () => void;
}

export interface PhoneState {
  battery: number;
  health: number;
  cpuUsage: number;
  ramUsage: number;
  filesDeleted: number;
  virusCount: number;
  antivirusActive: boolean;
  firewallActive: boolean;
}

export interface AttackLog {
  id: string;
  type: string;
  damage: number;
  timestamp: number;
  source: 'player' | 'bot';
}

export function GameInterface({ onBack, onWin, showTutorial, onSkipTutorial }: GameInterfaceProps) {
  const [playerPhone, setPlayerPhone] = useState<PhoneState>({
    battery: 100,
    health: 100,
    cpuUsage: 20,
    ramUsage: 30,
    filesDeleted: 0,
    virusCount: 0,
    antivirusActive: false,
    firewallActive: false,
  });

  const [botPhone, setBotPhone] = useState<PhoneState>({
    battery: 100,
    health: 100,
    cpuUsage: 25,
    ramUsage: 35,
    filesDeleted: 0,
    virusCount: 0,
    antivirusActive: false,
    firewallActive: false,
  });

  const [attackLogs, setAttackLogs] = useState<AttackLog[]>([]);
  const [gameOver, setGameOver] = useState(false);
  const [winner, setWinner] = useState<'player' | 'bot' | null>(null);
  const [attackCount, setAttackCount] = useState(0);

  // Check for game over conditions
  useEffect(() => {
    if (playerPhone.health <= 0 || playerPhone.battery <= 0) {
      setGameOver(true);
      setWinner('bot');
    } else if (botPhone.health <= 0 || botPhone.battery <= 0) {
      setGameOver(true);
      setWinner('player');
      const earnedGB = Math.floor(attackCount * 2.5 + 50); // Base reward + attack bonus
      setTimeout(() => onWin(earnedGB), 2000);
    }
  }, [playerPhone.health, playerPhone.battery, botPhone.health, botPhone.battery, attackCount, onWin]);

  const addAttackLog = useCallback((type: string, damage: number, source: 'player' | 'bot') => {
    const log: AttackLog = {
      id: Date.now().toString(),
      type,
      damage,
      timestamp: Date.now(),
      source
    };
    setAttackLogs(prev => [log, ...prev.slice(0, 9)]); // Keep last 10 logs
  }, []);

  const attackBot = useCallback((attackType: string, damage: number) => {
    if (gameOver) return;
    
    setBotPhone(prev => {
      const newHealth = Math.max(0, prev.health - damage);
      const newBattery = attackType === 'battery_drain' ? Math.max(0, prev.battery - damage) : prev.battery;
      const newVirusCount = attackType === 'virus' ? prev.virusCount + 1 : prev.virusCount;
      const newFilesDeleted = attackType === 'file_delete' ? prev.filesDeleted + 1 : prev.filesDeleted;
      
      return {
        ...prev,
        health: newHealth,
        battery: newBattery,
        virusCount: newVirusCount,
        filesDeleted: newFilesDeleted,
        cpuUsage: Math.min(100, prev.cpuUsage + (attackType === 'cpu_overload' ? damage : 0)),
        ramUsage: Math.min(100, prev.ramUsage + (attackType === 'ram_flood' ? damage : 0))
      };
    });
    
    setAttackCount(prev => prev + 1);
    addAttackLog(attackType, damage, 'player');
  }, [gameOver, addAttackLog]);

  const defendPlayer = useCallback((defenseType: string) => {
    if (gameOver) return;
    
    setPlayerPhone(prev => {
      switch (defenseType) {
        case 'antivirus':
          return { ...prev, antivirusActive: true, virusCount: 0 };
        case 'firewall':
          return { ...prev, firewallActive: true };
        case 'repair':
          return { ...prev, health: Math.min(100, prev.health + 20) };
        case 'battery_saver':
          return { ...prev, battery: Math.min(100, prev.battery + 15) };
        default:
          return prev;
      }
    });
  }, [gameOver]);

  if (gameOver) {
    return (
      <div className="min-h-screen flex items-center justify-center p-4">
        <div className="bg-black/80 backdrop-blur-lg rounded-3xl p-8 max-w-md w-full border border-gray-700 text-center">
          <h2 className={`text-4xl font-bold mb-4 ${winner === 'player' ? 'text-green-400' : 'text-red-400'}`}>
            {winner === 'player' ? 'VICTORY!' : 'DEFEAT!'}
          </h2>
          <p className="text-gray-300 mb-6">
            {winner === 'player' 
              ? `You earned ${Math.floor(attackCount * 2.5 + 50)} GB!` 
              : 'Your phone was destroyed!'
            }
          </p>
          <Button 
            onClick={onBack}
            className="w-full bg-blue-600 hover:bg-blue-700 text-white py-4 text-lg font-semibold rounded-2xl"
          >
            Return to Menu
          </Button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-purple-900 to-gray-900 p-4">
      <div className="max-w-6xl mx-auto">
        <div className="flex justify-between items-center mb-4">
          <Button 
            onClick={onBack}
            variant="outline"
            className="bg-gray-800 border-gray-600 text-white hover:bg-gray-700"
          >
            ← Back
          </Button>
          <div className="text-white text-center">
            <h2 className="text-xl font-bold">SABOTAGE BATTLE</h2>
            <p className="text-sm text-gray-400">Attacks: {attackCount}</p>
          </div>
          <div className="w-20"></div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* Player Phone */}
          <div className="space-y-4">
            <h3 className="text-white text-lg font-semibold text-center">Your Phone</h3>
            <PhoneScreen 
              phoneState={playerPhone}
              setPhoneState={setPlayerPhone}
              isPlayer={true}
              onAttack={attackBot}
              onDefend={defendPlayer}
              showTutorial={showTutorial}
              onSkipTutorial={onSkipTutorial}
            />
          </div>

          {/* Bot Phone */}
          <div className="space-y-4">
            <h3 className="text-white text-lg font-semibold text-center">Enemy Phone</h3>
            <PhoneScreen 
              phoneState={botPhone}
              setPhoneState={setBotPhone}
              isPlayer={false}
              onAttack={() => {}}
              onDefend={() => {}}
              showTutorial={false}
              onSkipTutorial={() => {}}
            />
          </div>
        </div>

        {/* Attack Logs */}
        <div className="mt-8 bg-black/60 backdrop-blur-lg rounded-2xl p-4">
          <h4 className="text-white font-semibold mb-2">Battle Log</h4>
          <div className="space-y-1 max-h-32 overflow-y-auto">
            {attackLogs.map(log => (
              <div key={log.id} className={`text-sm ${log.source === 'player' ? 'text-green-400' : 'text-red-400'}`}>
                {log.source === 'player' ? '→' : '←'} {log.type.replace('_', ' ')} ({log.damage} damage)
              </div>
            ))}
          </div>
        </div>

        <BotAI 
          playerPhone={playerPhone}
          setPlayerPhone={setPlayerPhone}
          addAttackLog={addAttackLog}
          gameOver={gameOver}
        />
      </div>
    </div>
  );
}
