"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { PhoneState } from "@/components/GameInterface";

interface AntivirusAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function AntivirusApp({ phoneState, setPhoneState, onAttack, onDefend, isPlayer }: AntivirusAppProps) {
  const [scanning, setScanning] = useState(false);
  const [scanProgress, setScanProgress] = useState(0);
  const [lastScanTime, setLastScanTime] = useState<Date | null>(null);
  const [threats, setThreats] = useState<string[]>([]);

  useEffect(() => {
    // Generate threats based on phone state
    const currentThreats = [];
    if (phoneState.virusCount > 0) {
      for (let i = 0; i < phoneState.virusCount; i++) {
        currentThreats.push(`Trojan.Sabotage.${i + 1}`);
      }
    }
    if (phoneState.cpuUsage > 80) {
      currentThreats.push("Malware.CPUMiner");
    }
    if (phoneState.ramUsage > 80) {
      currentThreats.push("Spyware.MemoryLeech");
    }
    setThreats(currentThreats);
  }, [phoneState.virusCount, phoneState.cpuUsage, phoneState.ramUsage]);

  const startScan = () => {
    if (!isPlayer || scanning) return;
    
    setScanning(true);
    setScanProgress(0);
    
    const interval = setInterval(() => {
      setScanProgress(prev => {
        if (prev >= 100) {
          clearInterval(interval);
          setScanning(false);
          setLastScanTime(new Date());
          
          // Clean viruses if antivirus is active
          if (phoneState.antivirusActive) {
            setPhoneState(prevState => ({
              ...prevState,
              virusCount: 0,
              cpuUsage: Math.max(20, prevState.cpuUsage - 30),
              ramUsage: Math.max(30, prevState.ramUsage - 20)
            }));
          }
          
          return 100;
        }
        return prev + 2;
      });
    }, 100);
  };

  const activateRealTimeProtection = () => {
    if (!isPlayer) return;
    onDefend('antivirus');
  };

  const quarantineThreat = (threat: string) => {
    if (!isPlayer) return;
    setThreats(prev => prev.filter(t => t !== threat));
    setPhoneState(prev => ({
      ...prev,
      virusCount: Math.max(0, prev.virusCount - 1)
    }));
  };

  return (
    <div className="h-full space-y-4">
      {/* Antivirus Status */}
      <div className="bg-gray-800 rounded-lg p-4">
        <div className="flex justify-between items-center mb-3">
          <h3 className="text-white font-semibold">Protection Status</h3>
          <div className={`w-3 h-3 rounded-full ${phoneState.antivirusActive ? 'bg-green-500' : 'bg-red-500'}`}></div>
        </div>
        
        <div className="space-y-2">
          <div className="flex justify-between text-sm">
            <span className="text-gray-300">Real-time Protection</span>
            <span className={phoneState.antivirusActive ? 'text-green-400' : 'text-red-400'}>
              {phoneState.antivirusActive ? 'ON' : 'OFF'}
            </span>
          </div>
          
          <div className="flex justify-between text-sm">
            <span className="text-gray-300">Threats Detected</span>
            <span className={threats.length === 0 ? 'text-green-400' : 'text-red-400'}>
              {threats.length}
            </span>
          </div>
          
          <div className="flex justify-between text-sm">
            <span className="text-gray-300">Last Scan</span>
            <span className="text-gray-400">
              {lastScanTime ? lastScanTime.toLocaleTimeString() : 'Never'}
            </span>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Quick Actions</h3>
        <div className="space-y-2">
          <Button 
            onClick={startScan}
            disabled={!isPlayer || scanning}
            className="w-full bg-blue-600 hover:bg-blue-700"
          >
            {scanning ? 'Scanning...' : 'Quick Scan'}
          </Button>
          
          <Button 
            onClick={activateRealTimeProtection}
            disabled={!isPlayer || phoneState.antivirusActive}
            className="w-full bg-green-600 hover:bg-green-700"
          >
            {phoneState.antivirusActive ? 'Protection Active' : 'Enable Protection'}
          </Button>
        </div>
      </div>

      {/* Scan Progress */}
      {scanning && (
        <div className="bg-gray-800 rounded-lg p-4">
          <h3 className="text-white font-semibold mb-3">Scanning System</h3>
          <div className="space-y-2">
            <div className="bg-gray-700 rounded-full h-2">
              <div 
                className="bg-blue-500 h-full rounded-full transition-all duration-300"
                style={{ width: `${scanProgress}%` }}
              ></div>
            </div>
            <div className="text-center text-sm text-gray-300">
              {scanProgress}% Complete
            </div>
          </div>
        </div>
      )}

      {/* Threat List */}
      {threats.length > 0 && (
        <div className="bg-gray-800 rounded-lg p-4">
          <h3 className="text-white font-semibold mb-3">Detected Threats</h3>
          <div className="space-y-2 max-h-32 overflow-y-auto">
            {threats.map((threat, index) => (
              <div key={index} className="flex justify-between items-center bg-red-900/30 rounded p-2">
                <div>
                  <div className="text-red-400 text-sm font-medium">{threat}</div>
                  <div className="text-gray-400 text-xs">High Risk</div>
                </div>
                <Button 
                  onClick={() => quarantineThreat(threat)}
                  disabled={!isPlayer}
                  size="sm"
                  className="bg-red-600 hover:bg-red-700 text-xs"
                >
                  Quarantine
                </Button>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* System Health Impact */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Security Impact</h3>
        <div className="space-y-2 text-sm">
          {phoneState.antivirusActive ? (
            <div className="text-green-400 flex items-center">
              <span className="mr-2">🛡️</span>
              System protected from incoming attacks
            </div>
          ) : (
            <div className="text-red-400 flex items-center">
              <span className="mr-2">⚠️</span>
              System vulnerable to malware
            </div>
          )}
          
          {threats.length === 0 ? (
            <div className="text-green-400 flex items-center">
              <span className="mr-2">✅</span>
              No threats detected
            </div>
          ) : (
            <div className="text-red-400 flex items-center">
              <span className="mr-2">🦠</span>
              Active threats compromising system
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
