"use client";

import { PhoneState } from "@/components/GameInterface";

interface StatusAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function StatusApp({ phoneState }: StatusAppProps) {
  const getHealthColor = (value: number) => {
    if (value > 70) return "text-green-400";
    if (value > 40) return "text-yellow-400";
    return "text-red-400";
  };

  const getUsageColor = (value: number) => {
    if (value < 50) return "text-green-400";
    if (value < 80) return "text-yellow-400";
    return "text-red-400";
  };

  return (
    <div className="h-full space-y-4">
      {/* System Health */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">System Health</h3>
        <div className="space-y-3">
          <div>
            <div className="flex justify-between text-sm mb-1">
              <span className="text-gray-300">Overall Health</span>
              <span className={getHealthColor(phoneState.health)}>{phoneState.health}%</span>
            </div>
            <div className="bg-gray-700 rounded-full h-2">
              <div 
                className={`h-full rounded-full transition-all duration-300 ${
                  phoneState.health > 70 ? 'bg-green-500' : 
                  phoneState.health > 40 ? 'bg-yellow-500' : 'bg-red-500'
                }`}
                style={{ width: `${phoneState.health}%` }}
              ></div>
            </div>
          </div>
          
          <div>
            <div className="flex justify-between text-sm mb-1">
              <span className="text-gray-300">Battery Level</span>
              <span className={getHealthColor(phoneState.battery)}>{phoneState.battery}%</span>
            </div>
            <div className="bg-gray-700 rounded-full h-2">
              <div 
                className={`h-full rounded-full transition-all duration-300 ${
                  phoneState.battery > 50 ? 'bg-green-500' : 
                  phoneState.battery > 20 ? 'bg-yellow-500' : 'bg-red-500'
                }`}
                style={{ width: `${phoneState.battery}%` }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      {/* Resource Usage */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Resource Usage</h3>
        <div className="space-y-3">
          <div>
            <div className="flex justify-between text-sm mb-1">
              <span className="text-gray-300">CPU Usage</span>
              <span className={getUsageColor(phoneState.cpuUsage)}>{phoneState.cpuUsage}%</span>
            </div>
            <div className="bg-gray-700 rounded-full h-2">
              <div 
                className={`h-full rounded-full transition-all duration-300 ${
                  phoneState.cpuUsage < 50 ? 'bg-green-500' : 
                  phoneState.cpuUsage < 80 ? 'bg-yellow-500' : 'bg-red-500'
                }`}
                style={{ width: `${phoneState.cpuUsage}%` }}
              ></div>
            </div>
          </div>
          
          <div>
            <div className="flex justify-between text-sm mb-1">
              <span className="text-gray-300">RAM Usage</span>
              <span className={getUsageColor(phoneState.ramUsage)}>{phoneState.ramUsage}%</span>
            </div>
            <div className="bg-gray-700 rounded-full h-2">
              <div 
                className={`h-full rounded-full transition-all duration-300 ${
                  phoneState.ramUsage < 50 ? 'bg-green-500' : 
                  phoneState.ramUsage < 80 ? 'bg-yellow-500' : 'bg-red-500'
                }`}
                style={{ width: `${phoneState.ramUsage}%` }}
              ></div>
            </div>
          </div>
        </div>
      </div>

      {/* Security Status */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Security Status</h3>
        <div className="space-y-2">
          <div className="flex justify-between items-center">
            <span className="text-gray-300 text-sm">Antivirus</span>
            <span className={`text-sm ${phoneState.antivirusActive ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.antivirusActive ? 'Active' : 'Inactive'}
            </span>
          </div>
          
          <div className="flex justify-between items-center">
            <span className="text-gray-300 text-sm">Firewall</span>
            <span className={`text-sm ${phoneState.firewallActive ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.firewallActive ? 'Active' : 'Inactive'}
            </span>
          </div>
          
          <div className="flex justify-between items-center">
            <span className="text-gray-300 text-sm">Virus Count</span>
            <span className={`text-sm ${phoneState.virusCount === 0 ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.virusCount}
            </span>
          </div>
          
          <div className="flex justify-between items-center">
            <span className="text-gray-300 text-sm">Files Deleted</span>
            <span className={`text-sm ${phoneState.filesDeleted === 0 ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.filesDeleted}
            </span>
          </div>
        </div>
      </div>

      {/* System Alerts */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">System Alerts</h3>
        <div className="space-y-2 text-sm">
          {phoneState.health < 25 && (
            <div className="text-red-400 flex items-center">
              <span className="mr-2">⚠️</span>
              Critical system health detected!
            </div>
          )}
          {phoneState.battery < 20 && (
            <div className="text-red-400 flex items-center">
              <span className="mr-2">🔋</span>
              Low battery warning!
            </div>
          )}
          {phoneState.virusCount > 0 && (
            <div className="text-red-400 flex items-center">
              <span className="mr-2">🦠</span>
              {phoneState.virusCount} virus(es) detected!
            </div>
          )}
          {phoneState.cpuUsage > 90 && (
            <div className="text-yellow-400 flex items-center">
              <span className="mr-2">🔥</span>
              CPU overheating!
            </div>
          )}
          {phoneState.ramUsage > 90 && (
            <div className="text-yellow-400 flex items-center">
              <span className="mr-2">💾</span>
              Memory critically low!
            </div>
          )}
          {phoneState.health > 80 && phoneState.virusCount === 0 && phoneState.battery > 50 && (
            <div className="text-green-400 flex items-center">
              <span className="mr-2">✅</span>
              All systems operational
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
