"use client";

import { useEffect, useCallback } from "react";
import { PhoneState, AttackLog } from "@/components/GameInterface";

interface BotAIProps {
  playerPhone: PhoneState;
  setPlayerPhone: React.Dispatch<React.SetStateAction<PhoneState>>;
  addAttackLog: (type: string, damage: number, source: 'player' | 'bot') => void;
  gameOver: boolean;
}

export function BotAI({ playerPhone, setPlayerPhone, addAttackLog, gameOver }: BotAIProps) {
  
  const botAttacks = [
    { type: 'virus', damage: 12, probability: 0.3, description: 'Bot deployed virus' },
    { type: 'battery_drain', damage: 8, probability: 0.25, description: 'Bot drained battery' },
    { type: 'file_delete', damage: 20, probability: 0.15, description: 'Bot deleted files' },
    { type: 'cpu_overload', damage: 10, probability: 0.2, description: 'Bot overloaded CPU' },
    { type: 'ram_flood', damage: 15, probability: 0.2, description: 'Bot flooded RAM' },
    { type: 'ddos', damage: 18, probability: 0.1, description: 'Bot launched DDoS' },
  ];

  const executeAttack = useCallback((attack: any) => {
    if (gameOver) return;

    // Reduce damage if player has defenses active
    let actualDamage = attack.damage;
    
    if (playerPhone.firewallActive) {
      actualDamage = Math.floor(actualDamage * 0.6); // Firewall reduces damage by 40%
    }
    
    if (playerPhone.antivirusActive && attack.type === 'virus') {
      actualDamage = Math.floor(actualDamage * 0.3); // Antivirus greatly reduces virus damage
    }

    setPlayerPhone(prev => {
      const newState = { ...prev };
      
      switch (attack.type) {
        case 'virus':
          newState.virusCount += 1;
          newState.health = Math.max(0, prev.health - actualDamage);
          newState.cpuUsage = Math.min(100, prev.cpuUsage + 5);
          break;
          
        case 'battery_drain':
          newState.battery = Math.max(0, prev.battery - actualDamage);
          break;
          
        case 'file_delete':
          newState.filesDeleted += 1;
          newState.health = Math.max(0, prev.health - actualDamage);
          break;
          
        case 'cpu_overload':
          newState.cpuUsage = Math.min(100, prev.cpuUsage + actualDamage);
          newState.health = Math.max(0, prev.health - Math.floor(actualDamage * 0.5));
          break;
          
        case 'ram_flood':
          newState.ramUsage = Math.min(100, prev.ramUsage + actualDamage);
          newState.health = Math.max(0, prev.health - Math.floor(actualDamage * 0.6));
          break;
          
        case 'ddos':
          newState.health = Math.max(0, prev.health - actualDamage);
          newState.cpuUsage = Math.min(100, prev.cpuUsage + 10);
          newState.ramUsage = Math.min(100, prev.ramUsage + 8);
          break;
          
        default:
          newState.health = Math.max(0, prev.health - actualDamage);
      }
      
      return newState;
    });

    addAttackLog(attack.type, actualDamage, 'bot');
  }, [gameOver, playerPhone.firewallActive, playerPhone.antivirusActive, setPlayerPhone, addAttackLog]);

  const selectBotAttack = useCallback(() => {
    if (gameOver) return;

    // Bot AI logic - choose attacks based on player's state
    let availableAttacks = [...botAttacks];
    
    // Prioritize certain attacks based on player state
    if (playerPhone.health < 30) {
      // Focus on finishing moves when player is low
      availableAttacks = availableAttacks.filter(a => 
        a.type === 'file_delete' || a.type === 'ddos' || a.type === 'virus'
      );
    } else if (playerPhone.battery < 40) {
      // Focus on battery drain when battery is low
      availableAttacks = availableAttacks.filter(a => 
        a.type === 'battery_drain' || a.type === 'cpu_overload'
      );
    } else if (!playerPhone.antivirusActive) {
      // Spam viruses if no antivirus
      availableAttacks = availableAttacks.filter(a => 
        a.type === 'virus' || a.type === 'ram_flood'
      );
    }

    // Select attack based on probability
    const totalProbability = availableAttacks.reduce((sum, attack) => sum + attack.probability, 0);
    let random = Math.random() * totalProbability;
    
    for (const attack of availableAttacks) {
      random -= attack.probability;
      if (random <= 0) {
        executeAttack(attack);
        break;
      }
    }
  }, [gameOver, playerPhone.health, playerPhone.battery, playerPhone.antivirusActive, botAttacks, executeAttack]);

  // Bot attack intervals
  useEffect(() => {
    if (gameOver) return;

    // Bot attacks every 3-8 seconds with some randomness
    const getRandomInterval = () => Math.random() * 5000 + 3000; // 3-8 seconds
    
    const scheduleNextAttack = () => {
      const interval = setTimeout(() => {
        // 70% chance to attack each interval
        if (Math.random() < 0.7) {
          selectBotAttack();
        }
        scheduleNextAttack(); // Schedule next attack
      }, getRandomInterval());
      
      return interval;
    };

    const timeoutId = scheduleNextAttack();
    
    return () => clearTimeout(timeoutId);
  }, [gameOver, selectBotAttack]);

  // Defensive actions for bot (simulated)
  useEffect(() => {
    if (gameOver) return;

    // Bot occasionally "defends" by reducing its vulnerability (this is just for realism)
    const defenseInterval = setInterval(() => {
      // This doesn't actually affect the bot's phone state since we don't show it
      // But it could be used for future enhancements
    }, 15000);

    return () => clearInterval(defenseInterval);
  }, [gameOver]);

  // Auto-disable defenses after some time (realism)
  useEffect(() => {
    if (playerPhone.firewallActive) {
      const timeout = setTimeout(() => {
        setPlayerPhone(prev => ({ ...prev, firewallActive: false }));
      }, 30000); // Firewall lasts 30 seconds
      
      return () => clearTimeout(timeout);
    }
  }, [playerPhone.firewallActive, setPlayerPhone]);

  useEffect(() => {
    if (playerPhone.antivirusActive) {
      const timeout = setTimeout(() => {
        setPlayerPhone(prev => ({ ...prev, antivirusActive: false }));
      }, 45000); // Antivirus lasts 45 seconds
      
      return () => clearTimeout(timeout);
    }
  }, [playerPhone.antivirusActive, setPlayerPhone]);

  // This component doesn't render anything visible
  return null;
}
