"use client";

import { useState, useEffect } from "react";
import { PhoneState } from "@/components/GameInterface";

interface Message {
  id: string;
  sender: string;
  content: string;
  timestamp: Date;
  type: 'tutorial' | 'alert' | 'tip';
}

interface MessagesAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function MessagesApp({ phoneState, isPlayer }: MessagesAppProps) {
  const [messages, setMessages] = useState<Message[]>([]);

  useEffect(() => {
    const initialMessages: Message[] = [
      {
        id: '1',
        sender: 'System Guide',
        content: 'Welcome to Sabotage! Your goal is to destroy the enemy phone while protecting yours. Use the Terminal app to launch attacks and other apps to defend.',
        timestamp: new Date(Date.now() - 300000),
        type: 'tutorial'
      },
      {
        id: '2',
        sender: 'Combat Tutorial',
        content: 'Attack Commands:\n• virus enemy - Deploy virus (15 damage)\n• ddos enemy - DDoS attack (20 damage)\n• battery_drain enemy - Drain battery (10 damage)\n• file_delete enemy - Delete files (25 damage)',
        timestamp: new Date(Date.now() - 240000),
        type: 'tutorial'
      },
      {
        id: '3',
        sender: 'Defense Tutorial',
        content: 'Defense Commands:\n• repair - Restore health\n• firewall - Block attacks\n• Use Antivirus app for virus protection\n• Monitor Status app for system health',
        timestamp: new Date(Date.now() - 180000),
        type: 'tutorial'
      },
      {
        id: '4',
        sender: 'Strategy Tips',
        content: 'Pro Tips:\n• Keep your antivirus active\n• Monitor your battery and health\n• Use file_delete for maximum damage\n• Repair when health is low',
        timestamp: new Date(Date.now() - 120000),
        type: 'tip'
      }
    ];

    setMessages(initialMessages);
  }, []);

  useEffect(() => {
    // Add dynamic messages based on phone state
    const newMessages: Message[] = [];

    if (phoneState.health < 30 && !messages.some(m => m.content.includes('Critical health'))) {
      newMessages.push({
        id: `alert-${Date.now()}`,
        sender: 'System Alert',
        content: 'Critical health detected! Use the Terminal command "repair" or visit the Security app to restore your system.',
        timestamp: new Date(),
        type: 'alert'
      });
    }

    if (phoneState.battery < 25 && !messages.some(m => m.content.includes('Low battery'))) {
      newMessages.push({
        id: `battery-${Date.now()}`,
        sender: 'Battery Alert',
        content: 'Low battery warning! Your phone will shut down if battery reaches 0%. Use Battery app to optimize power.',
        timestamp: new Date(),
        type: 'alert'
      });
    }

    if (phoneState.virusCount > 0 && !messages.some(m => m.content.includes('Virus detected'))) {
      newMessages.push({
        id: `virus-${Date.now()}`,
        sender: 'Security Alert',
        content: `Virus detected! ${phoneState.virusCount} threat(s) found. Open Antivirus app immediately to clean your system.`,
        timestamp: new Date(),
        type: 'alert'
      });
    }

    if (!phoneState.antivirusActive && !messages.some(m => m.content.includes('Enable antivirus'))) {
      newMessages.push({
        id: `antivirus-${Date.now()}`,
        sender: 'Security Tip',
        content: 'Your antivirus is disabled! Enable real-time protection in the Antivirus app to defend against incoming attacks.',
        timestamp: new Date(),
        type: 'tip'
      });
    }

    if (newMessages.length > 0) {
      setMessages(prev => [...newMessages, ...prev]);
    }
  }, [phoneState.health, phoneState.battery, phoneState.virusCount, phoneState.antivirusActive, messages]);

  const getMessageIcon = (type: string) => {
    switch (type) {
      case 'tutorial': return '📚';
      case 'alert': return '⚠️';
      case 'tip': return '💡';
      default: return '💬';
    }
  };

  const getMessageColor = (type: string) => {
    switch (type) {
      case 'tutorial': return 'border-blue-500';
      case 'alert': return 'border-red-500';
      case 'tip': return 'border-yellow-500';
      default: return 'border-gray-500';
    }
  };

  return (
    <div className="h-full flex flex-col">
      <div className="flex-1 space-y-3 overflow-y-auto">
        {messages.map((message) => (
          <div key={message.id} className={`bg-gray-800 rounded-lg p-3 border-l-4 ${getMessageColor(message.type)}`}>
            <div className="flex items-center justify-between mb-2">
              <div className="flex items-center space-x-2">
                <span className="text-lg">{getMessageIcon(message.type)}</span>
                <span className="text-white font-semibold text-sm">{message.sender}</span>
              </div>
              <span className="text-gray-400 text-xs">
                {message.timestamp.toLocaleTimeString()}
              </span>
            </div>
            <p className="text-gray-200 text-sm whitespace-pre-line leading-relaxed">
              {message.content}
            </p>
          </div>
        ))}
      </div>

      {/* Quick Reference */}
      <div className="mt-4 bg-gray-800 rounded-lg p-3">
        <h4 className="text-white font-semibold text-sm mb-2">Quick Reference</h4>
        <div className="grid grid-cols-2 gap-2 text-xs">
          <div className="text-gray-300">
            <div className="text-red-400 font-medium">Attack Apps:</div>
            <div>• Terminal</div>
            <div>• Security</div>
          </div>
          <div className="text-gray-300">
            <div className="text-green-400 font-medium">Defense Apps:</div>
            <div>• Antivirus</div>
            <div>• Battery</div>
            <div>• Settings</div>
          </div>
        </div>
      </div>

      {/* Current Status Summary */}
      <div className="mt-2 bg-gray-800 rounded-lg p-3">
        <h4 className="text-white font-semibold text-sm mb-2">System Summary</h4>
        <div className="grid grid-cols-2 gap-2 text-xs">
          <div className={`${phoneState.health > 50 ? 'text-green-400' : 'text-red-400'}`}>
            Health: {phoneState.health}%
          </div>
          <div className={`${phoneState.battery > 30 ? 'text-green-400' : 'text-red-400'}`}>
            Battery: {phoneState.battery}%
          </div>
          <div className={`${phoneState.antivirusActive ? 'text-green-400' : 'text-red-400'}`}>
            Antivirus: {phoneState.antivirusActive ? 'ON' : 'OFF'}
          </div>
          <div className={`${phoneState.virusCount === 0 ? 'text-green-400' : 'text-red-400'}`}>
            Viruses: {phoneState.virusCount}
          </div>
        </div>
      </div>
    </div>
  );
}
