"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Switch } from "@/components/ui/switch";
import { PhoneState } from "@/components/GameInterface";

interface BatteryAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function BatteryApp({ phoneState, setPhoneState, onAttack, onDefend, isPlayer }: BatteryAppProps) {
  const [powerSaverMode, setPowerSaverMode] = useState(false);
  const [backgroundApps, setBackgroundApps] = useState(true);
  const [screenBrightness, setScreenBrightness] = useState(75);

  const getBatteryColor = () => {
    if (phoneState.battery > 50) return "text-green-400";
    if (phoneState.battery > 20) return "text-yellow-400";
    return "text-red-400";
  };

  const getBatteryIcon = () => {
    if (phoneState.battery > 75) return "🔋";
    if (phoneState.battery > 50) return "🔋";
    if (phoneState.battery > 25) return "🪫";
    return "🪫";
  };

  const activatePowerSaver = () => {
    if (!isPlayer) return;
    
    setPowerSaverMode(true);
    setPhoneState(prev => ({
      ...prev,
      cpuUsage: Math.max(10, prev.cpuUsage - 20),
      ramUsage: Math.max(20, prev.ramUsage - 15)
    }));
    onDefend('battery_saver');
  };

  const optimizeBattery = () => {
    if (!isPlayer) return;
    
    setPhoneState(prev => ({
      ...prev,
      battery: Math.min(100, prev.battery + 15),
      cpuUsage: Math.max(15, prev.cpuUsage - 10)
    }));
  };

  const emergencyCharge = () => {
    if (!isPlayer) return;
    
    setPhoneState(prev => ({
      ...prev,
      battery: Math.min(100, prev.battery + 25),
      health: Math.max(0, prev.health - 10) // Costs health for emergency charge
    }));
  };

  const closeBackgroundApps = () => {
    if (!isPlayer) return;
    
    setBackgroundApps(false);
    setPhoneState(prev => ({
      ...prev,
      battery: Math.min(100, prev.battery + 10),
      cpuUsage: Math.max(10, prev.cpuUsage - 15),
      ramUsage: Math.max(20, prev.ramUsage - 20)
    }));
  };

  const getBatteryStatus = () => {
    if (phoneState.battery > 80) return "Excellent";
    if (phoneState.battery > 60) return "Good";
    if (phoneState.battery > 40) return "Fair";
    if (phoneState.battery > 20) return "Low";
    return "Critical";
  };

  const getEstimatedTime = () => {
    const drainRate = (phoneState.cpuUsage + phoneState.ramUsage) / 20;
    const timeLeft = phoneState.battery / drainRate;
    const hours = Math.floor(timeLeft / 60);
    const minutes = Math.floor(timeLeft % 60);
    return `${hours}h ${minutes}m`;
  };

  return (
    <div className="h-full space-y-4">
      {/* Battery Status */}
      <div className="bg-gray-800 rounded-lg p-4">
        <div className="flex items-center justify-between mb-3">
          <h3 className="text-white font-semibold">Battery Status</h3>
          <span className="text-2xl">{getBatteryIcon()}</span>
        </div>
        
        <div className="space-y-3">
          <div className="text-center">
            <div className={`text-4xl font-bold ${getBatteryColor()}`}>
              {phoneState.battery}%
            </div>
            <div className="text-gray-400 text-sm">{getBatteryStatus()}</div>
          </div>
          
          <div className="bg-gray-700 rounded-full h-4">
            <div 
              className={`h-full rounded-full transition-all duration-300 ${
                phoneState.battery > 50 ? 'bg-green-500' : 
                phoneState.battery > 20 ? 'bg-yellow-500' : 'bg-red-500'
              }`}
              style={{ width: `${phoneState.battery}%` }}
            ></div>
          </div>
          
          <div className="flex justify-between text-sm">
            <span className="text-gray-300">Time Remaining</span>
            <span className="text-white">{getEstimatedTime()}</span>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Quick Actions</h3>
        <div className="grid grid-cols-2 gap-2">
          <Button 
            onClick={activatePowerSaver}
            disabled={!isPlayer || powerSaverMode}
            className="bg-green-600 hover:bg-green-700 text-xs"
          >
            {powerSaverMode ? 'Power Saver ON' : 'Power Saver'}
          </Button>
          
          <Button 
            onClick={optimizeBattery}
            disabled={!isPlayer}
            className="bg-blue-600 hover:bg-blue-700 text-xs"
          >
            Optimize
          </Button>
          
          <Button 
            onClick={closeBackgroundApps}
            disabled={!isPlayer || !backgroundApps}
            className="bg-yellow-600 hover:bg-yellow-700 text-xs"
          >
            Close Apps
          </Button>
          
          <Button 
            onClick={emergencyCharge}
            disabled={!isPlayer}
            className="bg-red-600 hover:bg-red-700 text-xs"
          >
            Emergency
          </Button>
        </div>
      </div>

      {/* Battery Settings */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Battery Settings</h3>
        <div className="space-y-4">
          <div className="flex justify-between items-center">
            <div>
              <div className="text-white text-sm">Power Saver Mode</div>
              <div className="text-gray-400 text-xs">Reduce performance to save battery</div>
            </div>
            <Switch 
              checked={powerSaverMode}
              onCheckedChange={(checked) => {
                if (checked) activatePowerSaver();
                else setPowerSaverMode(false);
              }}
              disabled={!isPlayer}
            />
          </div>
          
          <div className="flex justify-between items-center">
            <div>
              <div className="text-white text-sm">Background Apps</div>
              <div className="text-gray-400 text-xs">Allow apps to run in background</div>
            </div>
            <Switch 
              checked={backgroundApps}
              onCheckedChange={setBackgroundApps}
              disabled={!isPlayer}
            />
          </div>
        </div>
      </div>

      {/* Battery Usage */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Battery Usage</h3>
        <div className="space-y-2">
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <span>💻</span>
              <span className="text-white text-sm">System</span>
            </div>
            <span className="text-gray-400 text-sm">{phoneState.cpuUsage}%</span>
          </div>
          
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <span>📱</span>
              <span className="text-white text-sm">Apps</span>
            </div>
            <span className="text-gray-400 text-sm">{phoneState.ramUsage}%</span>
          </div>
          
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <span>📶</span>
              <span className="text-white text-sm">Network</span>
            </div>
            <span className="text-gray-400 text-sm">15%</span>
          </div>
          
          <div className="flex justify-between items-center">
            <div className="flex items-center space-x-2">
              <span>🔆</span>
              <span className="text-white text-sm">Display</span>
            </div>
            <span className="text-gray-400 text-sm">20%</span>
          </div>
        </div>
      </div>

      {/* Battery Health */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Battery Health</h3>
        <div className="space-y-2 text-sm">
          <div className="flex justify-between">
            <span className="text-gray-300">Maximum Capacity</span>
            <span className="text-white">100%</span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-300">Peak Performance</span>
            <span className={phoneState.battery > 30 ? 'text-green-400' : 'text-red-400'}>
              {phoneState.battery > 30 ? 'Normal' : 'Degraded'}
            </span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-300">Charge Cycles</span>
            <span className="text-white">247</span>
          </div>
        </div>
      </div>

      {/* Warnings */}
      {phoneState.battery < 20 && (
        <div className="bg-red-900/30 rounded-lg p-4 border border-red-500/30">
          <div className="flex items-center space-x-2">
            <span className="text-red-400 text-lg">⚠️</span>
            <div>
              <div className="text-red-400 font-semibold text-sm">Low Battery Warning</div>
              <div className="text-red-300 text-xs">
                Your phone will shut down soon. Use emergency charge or power saver mode.
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
