"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { PhoneState } from "@/components/GameInterface";

interface SecurityAppProps {
  phoneState: PhoneState;
  setPhoneState: React.Dispatch<React.SetStateAction<PhoneState>>;
  onAttack: (attackType: string, damage: number) => void;
  onDefend: (defenseType: string) => void;
  isPlayer: boolean;
}

export function SecurityApp({ phoneState, setPhoneState, onAttack, onDefend, isPlayer }: SecurityAppProps) {
  const [selectedAttack, setSelectedAttack] = useState<string | null>(null);
  const [cooldowns, setCooldowns] = useState<{[key: string]: number}>({});

  const attacks = [
    {
      id: 'virus_bomb',
      name: 'Virus Bomb',
      damage: 30,
      description: 'Deploy multiple viruses simultaneously',
      icon: '💣',
      cooldown: 10000,
      color: 'bg-red-600'
    },
    {
      id: 'system_crash',
      name: 'System Crash',
      damage: 35,
      description: 'Force critical system failure',
      icon: '💥',
      cooldown: 15000,
      color: 'bg-orange-600'
    },
    {
      id: 'data_wipe',
      name: 'Data Wipe',
      damage: 40,
      description: 'Delete all user data and files',
      icon: '🗑️',
      cooldown: 20000,
      color: 'bg-purple-600'
    },
    {
      id: 'network_flood',
      name: 'Network Flood',
      damage: 25,
      description: 'Overwhelm network connections',
      icon: '🌊',
      cooldown: 8000,
      color: 'bg-blue-600'
    },
    {
      id: 'memory_bomb',
      name: 'Memory Bomb',
      damage: 28,
      description: 'Consume all available RAM',
      icon: '💾',
      cooldown: 12000,
      color: 'bg-yellow-600'
    },
    {
      id: 'rootkit',
      name: 'Rootkit',
      damage: 45,
      description: 'Gain deep system access',
      icon: '🔓',
      cooldown: 25000,
      color: 'bg-gray-600'
    }
  ];

  const defenses = [
    {
      id: 'firewall_max',
      name: 'Maximum Firewall',
      description: 'Enable strongest firewall protection',
      icon: '🛡️',
      color: 'bg-green-600'
    },
    {
      id: 'system_restore',
      name: 'System Restore',
      description: 'Restore system to previous state',
      icon: '🔄',
      color: 'bg-blue-600'
    },
    {
      id: 'isolation_mode',
      name: 'Isolation Mode',
      description: 'Disconnect from all networks',
      icon: '🔒',
      color: 'bg-purple-600'
    },
    {
      id: 'emergency_backup',
      name: 'Emergency Backup',
      description: 'Create instant system backup',
      icon: '💾',
      color: 'bg-yellow-600'
    }
  ];

  const executeAttack = (attack: any) => {
    if (!isPlayer || cooldowns[attack.id]) return;
    
    onAttack(attack.id, attack.damage);
    
    // Set cooldown
    setCooldowns(prev => ({ ...prev, [attack.id]: Date.now() + attack.cooldown }));
    
    // Clear cooldown after time
    setTimeout(() => {
      setCooldowns(prev => {
        const newCooldowns = { ...prev };
        delete newCooldowns[attack.id];
        return newCooldowns;
      });
    }, attack.cooldown);
  };

  const executeDefense = (defense: any) => {
    if (!isPlayer) return;
    
    switch (defense.id) {
      case 'firewall_max':
        onDefend('firewall');
        setPhoneState(prev => ({ ...prev, firewallActive: true }));
        break;
      case 'system_restore':
        setPhoneState(prev => ({
          ...prev,
          health: Math.min(100, prev.health + 40),
          virusCount: Math.max(0, prev.virusCount - 2),
          cpuUsage: Math.max(20, prev.cpuUsage - 30),
          ramUsage: Math.max(30, prev.ramUsage - 25)
        }));
        break;
      case 'isolation_mode':
        setPhoneState(prev => ({ ...prev, firewallActive: true }));
        break;
      case 'emergency_backup':
        onDefend('backup');
        break;
    }
  };

  const getCooldownTime = (attackId: string) => {
    const cooldownEnd = cooldowns[attackId];
    if (!cooldownEnd) return 0;
    return Math.max(0, cooldownEnd - Date.now());
  };

  return (
    <div className="h-full space-y-4">
      {/* Security Status */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Security Center</h3>
        <div className="grid grid-cols-2 gap-4 text-sm">
          <div className="text-center">
            <div className={`text-2xl ${phoneState.firewallActive ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.firewallActive ? '🛡️' : '⚠️'}
            </div>
            <div className="text-white">Firewall</div>
            <div className={phoneState.firewallActive ? 'text-green-400' : 'text-red-400'}>
              {phoneState.firewallActive ? 'Active' : 'Inactive'}
            </div>
          </div>
          
          <div className="text-center">
            <div className={`text-2xl ${phoneState.antivirusActive ? 'text-green-400' : 'text-red-400'}`}>
              {phoneState.antivirusActive ? '🔒' : '🔓'}
            </div>
            <div className="text-white">Protection</div>
            <div className={phoneState.antivirusActive ? 'text-green-400' : 'text-red-400'}>
              {phoneState.antivirusActive ? 'Secured' : 'Vulnerable'}
            </div>
          </div>
        </div>
      </div>

      {/* Attack Arsenal */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Attack Arsenal</h3>
        <div className="grid grid-cols-2 gap-2">
          {attacks.map((attack) => {
            const cooldownTime = getCooldownTime(attack.id);
            const isOnCooldown = cooldownTime > 0;
            
            return (
              <Button
                key={attack.id}
                onClick={() => executeAttack(attack)}
                disabled={!isPlayer || isOnCooldown}
                className={`${attack.color} hover:opacity-80 text-xs p-2 h-auto flex flex-col items-center space-y-1`}
              >
                <span className="text-lg">{attack.icon}</span>
                <span className="font-semibold">{attack.name}</span>
                <span className="text-xs opacity-80">{attack.damage} DMG</span>
                {isOnCooldown && (
                  <span className="text-xs text-red-300">
                    {Math.ceil(cooldownTime / 1000)}s
                  </span>
                )}
              </Button>
            );
          })}
        </div>
      </div>

      {/* Defense Systems */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Defense Systems</h3>
        <div className="grid grid-cols-2 gap-2">
          {defenses.map((defense) => (
            <Button
              key={defense.id}
              onClick={() => executeDefense(defense)}
              disabled={!isPlayer}
              className={`${defense.color} hover:opacity-80 text-xs p-2 h-auto flex flex-col items-center space-y-1`}
            >
              <span className="text-lg">{defense.icon}</span>
              <span className="font-semibold text-center leading-tight">{defense.name}</span>
            </Button>
          ))}
        </div>
      </div>

      {/* Attack Details */}
      {selectedAttack && (
        <div className="bg-gray-800 rounded-lg p-4">
          <h3 className="text-white font-semibold mb-3">Attack Details</h3>
          {attacks.map((attack) => (
            attack.id === selectedAttack && (
              <div key={attack.id} className="space-y-2">
                <div className="flex items-center space-x-2">
                  <span className="text-2xl">{attack.icon}</span>
                  <div>
                    <div className="text-white font-semibold">{attack.name}</div>
                    <div className="text-red-400">Damage: {attack.damage}</div>
                  </div>
                </div>
                <p className="text-gray-300 text-sm">{attack.description}</p>
                <div className="text-xs text-gray-400">
                  Cooldown: {attack.cooldown / 1000}s
                </div>
              </div>
            )
          ))}
        </div>
      )}

      {/* Security Logs */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Security Logs</h3>
        <div className="space-y-1 text-xs max-h-24 overflow-y-auto">
          <div className="text-green-400">✓ Security app initialized</div>
          <div className="text-yellow-400">⚠ System vulnerability detected</div>
          {phoneState.virusCount > 0 && (
            <div className="text-red-400">✗ {phoneState.virusCount} active threat(s)</div>
          )}
          {phoneState.firewallActive && (
            <div className="text-green-400">✓ Firewall protection enabled</div>
          )}
          {phoneState.health < 50 && (
            <div className="text-red-400">✗ System integrity compromised</div>
          )}
        </div>
      </div>

      {/* Threat Level */}
      <div className="bg-gray-800 rounded-lg p-4">
        <h3 className="text-white font-semibold mb-3">Threat Level</h3>
        <div className="flex items-center justify-between">
          <span className="text-gray-300">Current Level</span>
          <span className={`font-bold ${
            phoneState.health > 70 ? 'text-green-400' : 
            phoneState.health > 40 ? 'text-yellow-400' : 'text-red-400'
          }`}>
            {phoneState.health > 70 ? 'LOW' : 
             phoneState.health > 40 ? 'MEDIUM' : 'HIGH'}
          </span>
        </div>
        <div className="mt-2 bg-gray-700 rounded-full h-2">
          <div 
            className={`h-full rounded-full transition-all duration-300 ${
              phoneState.health > 70 ? 'bg-green-500' : 
              phoneState.health > 40 ? 'bg-yellow-500' : 'bg-red-500'
            }`}
            style={{ width: `${100 - phoneState.health}%` }}
          ></div>
        </div>
      </div>
    </div>
  );
}
